/**
 * 
 * @param num value
 * @param identifier  
 * @param n 
 */
export function PrefixInteger (num, identifier = 0, n) {
  return (new Array(n).join(identifier) + num).slice(-n)
}
/**
 **  
 **/
export function throttle (func, wait) {
  let lastTime = null
  let timeout
  return () => {
    let context = this
    let now = new Date()
    // 
    if (now - lastTime - wait > 0) {
      // 
      if (timeout) {
        clearTimeout(timeout)
        timeout = null
      }
      func.apply(context, arguments)
      lastTime = now
    } else if (!timeout) {
      timeout = setTimeout(() => {
        // 
        func.apply(context, arguments)
      }, wait)
    }
  }
}



